.rehab-swiper__city {
  padding-bottom: @d4px;

  font-size: @d27px;
  letter-spacing: 0.14px;
}

.rehab-swiper__street {
  padding-bottom: @d26px;

  font-size: @d30px;
  font-weight: 700;
  letter-spacing: 0.15px;

  color: #2f2f2f;
}

.rehab-swiper__place-description {
  // min-height: @d50px;
  width: @d338px;
  padding-bottom: @d17px;

  font-size: @d18px;
  line-height: @d24px;
  letter-spacing: 0.18px;
}

.rehab-swiper__price {
  margin-top: auto;

  font-size: @d23px;
  font-weight: 700;
  line-height: @d50px;
  letter-spacing: 0.23px;

  color: #2f2f2f;
}

.rehab-swiper__link-page {
  position: absolute;
  bottom: @d50px;
  right: @d72px;

  max-width: 33px;
  max-height: 33px;
  width: @d33px;
  height: @d33px;

  text-align: center;
  text-decoration: none;
  color: #dadada;

  & img {
    width: 100%;
    height: 100%;
  }
}

.double-main-wrapper {
  .rehab-swiper {
    margin-bottom: @d206px;
  }
  .rehab-swiper .swiper-container {
    box-shadow: 0 0 153px 27px rgba(17, 30, 66, 0.05);
  }
  .rehab-swiper__img-wrapper {
    height: @d278px;
  }
  .rehab-swiper__text-wrapper {
    // height: @d300px;
    height: @d230px;
    padding-top: @d29px;
    .padding-inline(@d33px);
    padding-bottom: @d23px;
  }
  .rehab-swiper__city {
    font-size: @d20px;
  }
  .rehab-swiper__street {
    margin-bottom: @d2px;
    font-size: @d22px;
  }
  .rehab-swiper__place-description {
    margin-bottom: @d9px;
    font-size: @d14px;
    line-height: normal;
  }
  .rehab-swiper__price {
    font-size: @d17px;
    line-height: normal;
  }
  .rehab-swiper__link-page {
    bottom: @d24px;
    right: @d45px;

    width: @d25px;
    height: @d25px;
  }
}

.rehab-swiper--two-row {
  .rehab-swiper__img-wrapper {
    max-height: none;
    height: @d278px;
  }

  .swiper-container {
    margin-bottom: 45px;

    box-shadow: 0 0 50px 40px rgba(17, 30, 66, 0.05);
  }

  .rehab-swiper__text-wrapper {
    // height: @d320px;
    height: @d250px;
    padding-top: @d27px;
    .padding-inline(@d30px);
    padding-bottom: @d43px;
  }

  .rehab-swiper__city {
    font-size: @d20px;
  }

  .rehab-swiper__street {
    padding-bottom: @d18px;
    font-size: @d22px;
  }

  .rehab-swiper__place-description {
    width: @d265px;
    padding-bottom: @d22px;

    font-size: @d14px;
    line-height: @d17px;
  }

  .rehab-swiper__price {
    font-size: @d17px;
    line-height: normal;
  }

  .rehab-swiper__link-page {
    position: absolute;
    bottom: @d45px;
    right: @d45px;

    max-width: none;
    max-height: none;
    width: @d24px;
    height: @d24px;
  }

  .rehab-swiper__pagination-wrapper {
    position: relative;

    width: @d440px;
    .margin-inline(@a);

    .swiper-button-prev,
    .swiper-button-next {
      color: #dbdbdb!important;
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    &::after {
      font-size: @d17px;
    }
  }

  .pagination {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: @d300px;
    height: @d60px;
    .margin-inline(@a);
  }

  .swiper-pagination {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
  }

  .swiper-pagination-bullet {
    width: auto;
    height: auto;

    font-size: @d23px;
    font-weight: 400;
    letter-spacing: 0.12px;

    border: none;
    background: none;

    &-active {
      position: relative;
      color: #ffffff;
      &::before {
        content: '';
        position: absolute;
        z-index: -1;

        top: -50%;
        left: 50%;
        transform: translate(-50%);

        width: @d52px;
        height: @d53px;
        background-color: #14b250;
        border-radius: 50%;
      }
    }
  }
}

.arrow-container .two-slides,
.two-slides {
  box-shadow: none;
  overflow: visible;
}

@media (max-width: @mobile-width) {
  .arrow-container {
    width: auto;
    .margin-inline(0);
  }

  .arrow-container .swiper-button-next,
  .arrow-container .swiper-button-prev {
    display: none;
  }

  .arrow-container .container {
    width: auto;
    .margin-inline(0);
  }

  .rehab-swiper,
  .double-main-wrapper .rehab-swiper,
  .rehab-swiper--two-row .rehab-swiper {
    margin-bottom: @m162px;
  }

  .rehab-swiper__link-pages {
    position: relative;
    display: inline-block;
    font-size: @m30px;
    margin-bottom: @m44px;

    &::before {
      content: '';
      position: absolute;
      top: -@m4px;
      right: -@m30px;

      width: @m14px;
      height: @m44px;

      background-image: url('../img/svg/arrow-m.svg');
      background-position: center;
      background-size: cover;
    }
  }

  .rehab-swiper__img-wrapper,
  .double-main-wrapper .rehab-swiper__img-wrapper,
  .rehab-swiper--two-row .rehab-swiper__img-wrapper {
    max-height: none;
    height: @m372px;
  }


  .rehab-swiper__text-wrapper,
  .double-main-wrapper .rehab-swiper__text-wrapper,
  .rehab-swiper--two-row .rehab-swiper__text-wrapper {
    // height: @m400px;
    height: @m350px;
    padding-top: @m37px;
    padding-left: @m37px;
    padding-right: @m23px;
    padding-bottom: @m40px;
  }

  .rehab-swiper__city,
  .double-main-wrapper .rehab-swiper__city,
  .rehab-swiper--two-row .rehab-swiper__city {
    padding-bottom: @m5px;
    font-size: @m27px;
  }

  .rehab-swiper__street,
  .double-main-wrapper .rehab-swiper__street,
  .rehab-swiper--two-row .rehab-swiper__street {
    padding-bottom: @m35px;

    font-size: @m30px;
    font-weight: 700;
  }

  .rehab-swiper__place-description,
  .double-main-wrapper .rehab-swiper__place-description,
  .rehab-swiper--two-row .rehab-swiper__place-description {
    width: 100%;
    min-height: @m85px;
    padding-bottom: @m25px;

    font-size: @m27px;
    line-height: @m28px;
  }

  .rehab-swiper__price,
  .double-main-wrapper .rehab-swiper__price,
  .rehab-swiper--two-row .rehab-swiper__price {
    margin-top: auto;

    font-size: @m30px;
    line-height: normal;
    letter-spacing: 0.23px;
  }

  .rehab-swiper__link-page,
  .double-main-wrapper .rehab-swiper__link-page,
  .rehab-swiper--two-row .rehab-swiper__link-page {
    bottom: @m47px;

    max-width: none;
    max-height: none;
    width: @m33px;
    height: @m33px;
  }

  .double-main-wrapper {
    .rehab-swiper .swiper-container {
      box-shadow: 0 0 153px 27px rgba(17, 30, 66, 0.05);
    }
    // .rehab-swiper__place-description {
    //   margin-bottom: 0;
